.hero-background{
  svg{
    font-size: 14px;
    font-weight: bold;
    width: 600px;
    height: 600px;
    @media (max-width: 750px){
      height: 500px;
      width: 500px;
    }
    @media (max-width: 500px){
      height: 400px;
      width: 100%;
    }

  }
  position: absolute;
  top: 50px;
  left: calc(50% - 300px);
  z-index: 9999;
  height: 600px;
  width: 600px;
  @media (max-width: 750px){
    top: 5px;
    height: 500px;
    width: 100%;
    left: 0;
  }
  @media (max-width: 500px){
    top: 5px;
    height: 400px;
    width: 100%;
    left: 0;
  }
}
.glyph{
  fill: #fff;
  filter: drop-shadow( 0px 0px 6px rgba(255, 255, 255, 1));
}

